<template>
  <div id="reveal-wrapper" class="reveal">
    <!--class="reveal"-->
    <div class="slides" v-if="!printMode">
      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
           
            <div class="controlX">
              <label class="back buttonX" @click="openChromeFlags()">
                Make sure you have enabled
                <b>Experimental Web Platform</b> feature in
                <b>chrome://flags</b>
              </label>
            </div>
            <div class="titleX">
              <label>Connect your Kiwrious sensor</label>
            </div>
            <iframe
              allowfullscreen="allowfullscreen"
              style="outline: none; border: none; margin-top: 40px;"
              width="890"
              height="450"
              src="https://www.youtube.com/embed/SStWqnO2IIk"
            ></iframe>
            <!-- <img v-if="!sensor_connected" id="connect-sensor-image" src="@/assets/guide.png" />
            <img v-if="sensor_connected" id="connect-sensor-image" src="@/assets/guide2.png" /> -->
          </div>
          <div class="right column">
            <div class="column" style="width: 100%; height: 88%;">
              <div style="width: 40%; height: auto;">
                <p
                  v-if="sensor_connected"
                  style="padding-top: 365px; font-size: 20px; text-align: center;"
                >
                  Sensor
                  <br />Connected!
                </p>
              </div>
              <img
                v-if="sensor_connected"
                src="@/assets/kiwrious_sensor.png"
                style="width: 40%; heigh:auto; margin-top: 0; border: none; box-shadow: none;"
              />
            </div>
            <div class="column" style="width: 100%; height: 12%; vertical-align: top;">
              <!--//simulator-->
              <button v-if="!sensor_connected" class="next buttonX" style="margin-top: 20px;"  @click="connectSensor()">Connect</button> 
              <button v-if="sensor_connected" class="next buttonX" style="margin-top: 20px;" @click="Next()">Next</button>
              <!--//sensor-->
              <!-- <serial @nuvData="nuvData" @nluxData="nluxData" @ncount="ncount" @nsensor_connected="nsensor_connected"></serial>
              <button v-if="sensor_connected" class="next buttonX" style="margin-top: 20px;" @click="Next()">Next</button> -->
            </div>
          </div>
        </div>
      </section>

      <!-- Title 0-->
      <section>
        <h2>UV Experiment</h2>
        <img class="long_img" src="@/assets/ZallyMaterial.png" />
        <p>Checkout what material can Protect Your Zally!</p>
        <button style="margin: 0;" class="next buttonX" @click="Next()">Let's do it!</button>
      </section>

      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label @click="Left()" class="back buttonX">&#10094; Back</label>
            </div>
            <div class="titleX">
              <label>Build a UV Shelter for zally!</label>
            </div>
            <div style="position: relative;">
              <iframe
                allowfullscreen="allowfullscreen"
                style="outline: none; border: none; margin-top: 40px;"
                width="890"
                height="450"
                src="https://www.youtube.com/embed/sZytuq_RYEA"
              ></iframe>
            </div>
          </div>
          <div class="right column">
            <button class="next buttonX" @click="Next();">Get Started</button>
          </div>
        </div>
      </section>

      <!-- self asses -->
      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label @click="Left()" class="back buttonX">&#10094; Back</label>
            </div>
            <div class="titleX questionX">
              <label>
                How much do you know about
                <br />Ultraviolet Light?
              </label>
            </div>
            <table id="expert-level">
              <tr>
                <td @click="learn_radio=1;">1</td>
                <td @click="learn_radio=2;">2</td>
                <td @click="learn_radio=3;">3</td>
                <td @click="learn_radio=4;">4</td>
                <td @click="learn_radio=5;">5</td>
              </tr>
            </table>
            <div class="column" style="margin-left:0; !important;">
              <div class="isinline" style="text-align: center;">
                I don't know
                <br />anything about it
              </div>
              <div class="isinline" style="width: 480px;"></div>
              <div class="isinline" style="text-align: center;">
                I'm an expert on
                <br />the subject!
              </div>
            </div>
          </div>
          <div class="right column"></div>
        </div>
      </section>

      <!-- Learn light 3 -->
      <learn :learn_radio="learn_radio"></learn>

      <!-- Learn How to -->
      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label @click="Jump(3,1)" class="back buttonX">&#10094; Back</label>
            </div>
            <div class="titleX">
              <label>Learn how to do the experiment</label>
            </div>
            <div style="position: relative;">
              <iframe
                allowfullscreen="allowfullscreen"
                style="outline: none; border: none; margin-top: 40px;"
                width="890"
                height="450"
                src="https://www.youtube.com/embed/YiFU5W-9-Y4"
              ></iframe>
            </div>
          </div>
          <div class="right column">
            <button class="next buttonX" @click="Next();">Skip video</button>
          </div>
        </div>
      </section>

      <!-- hardware test -->
      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label class="back buttonX" @click="Left()">&#10094; Back</label>
            </div>
            <div class="titleX">
              <label>Try out your Kiwrious Light sensor</label>
            </div>
            <div style="text-align: center; margin-top: 50px;">
              <div class="sensor-preview" @click="()=>{uvPreview = true}">
                <div id="uv-cover" class="column" v-if="!uvPreview">
                  <p style="font-size: 33px; font-weight: bold; color: gray;">
                    See
                    <br />UV
                    <br />Light
                    <br />Levels
                  </p>
                </div>
                <uvPreview v-if="uvPreview" :uvData="uvData"></uvPreview>
              </div>
              <div class="sensor-preview" @click="()=>{luxPreview = true}">
                <div id="lux-cover" class="column" v-if="!luxPreview">
                  <p style="font-size: 33px; font-weight: bold; color: gray;">
                    See
                    <br />Visible
                    <br />Light
                    <br />Levels
                  </p>
                </div>
                <luxPreview v-if="luxPreview" :luxData="luxData"></luxPreview>
              </div>
            </div>
            <div style="text-align: center;">
              <label class="questionX" v-show="uvPreview">Does your window glass block UV light?</label>
              <label class="questionX" v-show="luxPreview">Where is the brightest spot in the room?</label>
              <p
                style="text-align: right !important; margin-top: 75px; padding-right: 20px; font-size: 18px;"
                v-show="uvPreview && luxPreview"
              >Done exploring?</p>
            </div>
          </div>
          <div class="right column">
            <button v-show="uvPreview && luxPreview" class="next buttonX" @click="Next(); uvPreview=false; luxPreview=false;" 
            v-bind:style="waitedTooLong?'animation: highlight 1s infinite;':'animation: none;'">
              Start Experiment
            </button>
          </div>
        </div>
      </section>

      <!-- choose material -->
      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label class="back buttonX" @click="Left()">&#10094; Back</label>
            </div>
            <div class="middle_row">
              <div class="half column">
                <label class="titleX questionX">
                  What kind of
                  <br />materials would
                  <br />you like to test?
                </label>
                <label class="noteX">* Please choose 2 different materials</label>
                <img
                  style="width: 60%; height: auto; display:block; border: none; box-shadow: none;"
                  src="@/assets/ZallyMaterial.png"
                />
              </div>
              <div class="half column">
                <div class="tileX" @click="showAddNewTile()" v-if="materiallist.length < 8">
                  <img
                    v-if="!addingNew"
                    src="@/assets/addYourMaterial.png"
                    style="width: 100%; height: 100%; border: 2px solid #4874BF; margin:0; padding: 0; border-radius: 10px;"
                  />
                  <div v-if="!addingNew"></div>
                  <img
                    v-if="addingNew"
                    src="@/assets/No_Roof.png"
                    style="width: 100%; height: 100%; border: 2px solid white; margin:0; padding: 0; border-radius: 10px;"
                  />
                  <div v-if="addingNew">
                    <button
                      class="corner-button top-corner"
                      @click.stop="closeAddMaterialInput()"
                    >&#10006;</button>
                    <br />
                    <textarea
                      maxlength="20"
                      style="width: 85%; border: none; resize: none; text-align: center;"
                      id="material-input"
                      v-model="newMaterial"
                    ></textarea>
                    <br />
                    <button
                      class="corner-button bottom-corner"
                      @click.stop="addNewMaterial()"
                    >&#10010;</button>
                  </div>
                </div>
                <card
                  :id="'select-'+raw"
                  v-for="raw in materiallist"
                  v-bind:key="raw"
                  @select-material="selectMaterial(raw)"
                >
                  <img
                    :id="'select-'+raw +'-image'"
                    :src="getImgUrl(raw)"
                    style="width: 100%; height: 100%; border: 2px solid white; margin:0; padding: 0; border-radius: 10px;"
                  />
                  <div v-if="editMaterial != raw">
                    {{formatText(raw)}}
                    <button
                      v-if="materiallist.indexOf(raw)>5"
                      style="background-color: transparent; position: absolute; right: 5px; bottom: 5px; width: 25px; height: 25px; border: none; border-radius: 10px 0 10px 0; box-shadow: none; color: black; cursor: pointer;"
                      @click.stop="editThisMaterial(raw)"
                    >&#9998;</button>
                  </div>
                  <div v-if="editMaterial == raw">
                    <button
                      class="corner-button top-corner"
                      @click.stop="closeAddMaterialInput()"
                    >&#10006;</button>
                    <br />
                    <textarea
                      style="width: 85%; border: none; resize: none; text-align: center;"
                      id="edit-material"
                      v-model="editedMaterial"
                    ></textarea>
                    <br />
                    <button
                      class="corner-button bottom-corner"
                      @click.stop="updateMaterial()"
                    >&#10004;</button>
                  </div>
                </card>
                
                <label
                  v-if="checklist.length == 1"
                  class="noteX"
                >You need to select one more material</label>
                <label
                  v-if="checklist.length == 2"
                  class="noteX"
                >Great! You have selected 2 materials</label>
              </div>
            </div>
          </div>
          <div class="right column">
            <button v-show="checklist.length == 2" class="next buttonX" @click="Next()">Next</button>
          </div>
        </div>
      </section>

      <!-- UV ranking -->
      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label class="back buttonX" @click="Left()">&#10094; Back</label>
            </div>
            <div class="middle_row">
              <div class="half column">
                <label class="titleX">Hypothesis</label>
                <label class="questionX" style="margin-bottom: 30px;">
                  How well do you think each
                  <br />material will block UV light?
                </label>
                <board
                  id="stock1"
                  class="column"
                  style="width: 350px !important; height: 180px !important; border: none; margin-top: 10px; padding: 0 !important; text-align: left;"
                >
                  <card
                    class="draggable"
                    style="display: inline-block !important;"
                    v-bind:id="'card-uv-'+ material"
                    v-for="material in checklist"
                    v-bind:key="material"
                    draggable="true"
                  >
                    <img
                      :src="getImgUrl(material)"
                      style="width: 100%; height: 100%; border: none; margin:0; padding: 0; border-radius: 10px;"
                    />
                    <div
                      style="position: absolute; top: 0; bottom: 0;width: 100%; height: auto; text-align: center; font-size: 13px; display: flex; justify-content: center; align-items: center; white-space: pre; line-height: 95%"
                    >{{formatText(material)}}</div>
                  </card>
                </board>
                <label class="noteX">
                  Drag the materials to where you think
                  <br />they belong
                </label>
              </div>
              <div class="half column">
                <board class="column" id="board-uv-low">{{low_board}}</board>
                <board class="column" id="board-uv-high">{{high_board}}</board>
              </div>
            </div>
          </div>
          <div class="right column">
            <button class="next buttonX" @click="Next()">Next question</button>
          </div>
        </div>
      </section>

      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label class="back buttonX" @click="Left()">&#10094; Back</label>
            </div>
            <div class="middle_row" style="height: 550px; vertical-align: top">
              <div class="half column">
                <label class="titleX">Hypothesis</label>
                <label class="questionX" style="margin-bottom: 30px;">
                  How much visible light do you
                  <br />think each material will let
                  <br />through?
                </label>
                <board
                  id="stock2"
                  class="column"
                  style="width: 350px !important; height: 180px !important; border: none; margin-top: 10px; padding: 0 !important; text-align: left;"
                >
                  <card
                    v-bind:id="'card-lux-' + material"
                    v-for="material in checklist"
                    v-bind:key="material"
                    draggable="true"
                  >
                    <img
                      :src="getImgUrl(material)"
                      style="width: 100%; height: 100%; border: none; margin:0; padding: 0; border-radius: 10px;"
                    />
                    <div
                      style="position: absolute; top: 0; bottom: 0;width: 100%; height: auto; text-align: center; font-size: 13px; display: flex; justify-content: center; align-items: center; white-space: pre; line-height: 95%"
                    >{{formatText(material)}}</div>
                  </card>
                </board>
                <label class="noteX">
                  Drag the materials to where you think
                  <br />they belong
                </label>
              </div>
              <div class="half column">
                <board class="column" id="board-lux-low">Lets in very little light</board>
                <board class="column" id="board-lux-high">Lets in a lot of light</board>
              </div>
            </div>
          </div>
          <div class="right column">
            <button class="next buttonX" @click="Next()">Next question</button>
          </div>
        </div>
      </section>

      <!-- zally state ranking -->
      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label class="back buttonX" @click="Left()">&#10094; Back</label>
            </div>
            <div class="middle_row" style="height: 550px; vertical-align: top">
              <div class="half column">
                <label class="titleX">Hypothesis</label>
                <label class="questionX">
                  How do you think Zally will do
                  <br />with these roof materials?
                </label>
                <label
                  class="noteX"
                >Remember that Zally needs a material that protects him from UV light but lets enough visible light through!</label>
                <div style="width: 100%; height: 300px; margin-top: 20px;">
                  <card
                    class="tileX-mini"
                    v-bind:id="'hypo-' + zally"
                    v-for="zally in zallylist"
                    v-bind:key="zally"
                    draggable="true"
                  >
                    <img
                      :src="getImgUrl(zally)"
                      style="width: 100%; height: 100%; border: none; margin:0; padding: 0; box-shadow: none; outline: 1px solid lightgray;"
                    />
                    <div></div>
                  </card>
                </div>
              </div>
              <div class="half column" style="padding-top: 50px !important;">
                <div class="tileX">
                  <img
                    src="@/assets/No_Roof.png"
                    style="width: 100%; height: 100%; border: none; margin:0; padding: 0; border-radius: 10px;"
                    v-bind:class="{'borderX':false}"
                  />
                  <div>
                    No
                    <br />Roof
                  </div>
                </div>
                <div
                  style="margin-bottom: 0"
                  class="tileX"
                  v-for="material in checklist"
                  v-bind:key="material"
                >
                  <img
                    :src="getImgUrl(material)"
                    style="width: 100%; height: 100%; border: none; margin:0; padding: 0; border-radius: 10px;"
                    v-bind:class="{'borderX':false}"
                  />
                  <div>{{formatText(material)}}</div>
                </div>
                <img
                  class="vertical-connector"
                  v-for="i in 3"
                  v-bind:key="i"
                  src="@/assets/dashed_line.png"
                />
                <board
                  style="margin-top: 0"
                  v-bind:clone="true"
                  class="mini column"
                  id="zally-if-No_Roof"
                ></board>
                <board
                  v-for="material in checklist"
                  v-bind:key="'zally-if-'+material"
                  style="margin-top: 0"
                  v-bind:clone="true"
                  class="mini column"
                  v-bind:id="'zally-if-'+material"
                ></board>
              </div>
            </div>
          </div>
          <div class="right column">
            <button
              style="height: 60px;"
              v-if="hypothesisComplete"
              @click="generateHypothesis()"
              class="next buttonX"
            >Generate a hypothesis!</button>
          </div>
        </div>
      </section>

      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label class="back buttonX" @click="Left()">&#10094; Back</label>
            </div>
            <div class="titleX">
              <label>Hypothesis</label>
            </div>
            <label class="noteX">
              Great! You have come up with a good hypothesis to test!
              <br />Zally is very curious about the results!
            </label>

            <div v-if="sallyIfMaterial.happy.length!=0">
              <label class="questionX" style="line-height: 80% ">
                <b>
                  I think
                  <span>
                    <div
                      class="tileX"
                      style="display: inline-block; margin-right: 10px !important;"
                      v-for="happyMaterial in sallyIfMaterial.happy"
                      v-bind:key="'best-'+happyMaterial"
                    >
                      <img
                        :src="getImgUrl(happyMaterial)"
                        style="width: 100%; height: 100%; border: none; margin:0; padding: 0; border-radius: 10px;"
                        v-bind:class="{'borderX':false}"
                      />
                      <div
                        style="position: absolute; top: 0; bottom: 0;width: 100%; height: auto; text-align: center; font-size: 16px; display: flex; justify-content: center; align-items: center; white-space: pre; line-height: 95%"
                      >{{formatText(happyMaterial)}}</div>
                    </div>
                  </span>
                  will make a good roof for Zally
                </b>
              </label>
            </div>

            <div v-if="sallyIfMaterial.happy.length==0">
              <label class="questionX" style="line-height: 80% ">
                <b>
                  I think
                  <span style="font-size: 21px;">
                    <div
                      class="tileX"
                      style="display: inline-block; margin-right: 10px !important;"
                      v-for="material in checklist"
                      v-bind:key="'bad-'+material"
                    >
                      <img
                        :src="getImgUrl(material)"
                        style="width: 100%; height: 100%; border: none; margin:0; padding: 0; border-radius: 10px;"
                        v-bind:class="{'borderX':false}"
                      />
                      <div
                        style="position: absolute; top: 0; bottom: 0;width: 100%; height: auto; text-align: center; font-size: 16px; display: flex; justify-content: center; align-items: center; white-space: pre; line-height: 95%"
                      >{{formatText(material)}}</div>
                    </div>
                  </span>
                  will not work for Zally's roof
                </b>
              </label>
            </div>

            <div
              style="margin-left: 70px;"
              v-for="material in checklist"
              v-bind:key="'selected-material'+material"
            >
              <label class="questionX" style="line-height: 80%; margin-top: 0;">
                <span>
                  <div
                    class="tileX"
                    style="display: inline-block; margin-right: 0 !important; margin-top: 0;"
                  >
                    <img
                      :src="getImgUrl(material)"
                      style="width: 100%; height: 100%; border: none; margin:0; padding: 0; border-radius: 10px;"
                      v-bind:class="{'borderX':false}"
                    />
                    <div
                      style="position: absolute; top: 0; bottom: 0;width: 100%; height: auto; text-align: center; font-size: 16px; display: flex; justify-content: center; align-items: center; white-space: pre; line-height: 95%"
                    >{{formatText(material)}}</div>
                  </div>
                </span>
                {{generateSupportClause(material)}}
              </label>
            </div>

            <!-- <label v-if="hypothesis.bestMaterial.length!=0" class="questionX"> <span style="font-size: 22px; margin-left: 0; color: white"><b>I think</b></span> {{hypothesis.supportClause1}}</label> -->
            <!-- <label v-if="hypothesis.bestMaterial.length!=0" class="questionX"> <span style="font-size: 22px; margin-left:0; color: white;"><b>I think</b></span> {{hypothesis.supportClause2}}</label> -->
          </div>
          <div class="right column">
            <button class="next buttonX" @click="Next()">Let's test!</button>
          </div>
        </div>
      </section>

      <!-- Time to investigate -->
      <section>
        <div class="slideX">
          <div class="left column"></div>
          <div class="middle column">
            <div class="controlX">
              <label class="back buttonX" @click="Left()">&#10094; Back</label>
            </div>
            <div class="titleX">
              <label>Time to investigate!</label>
            </div>
            <div style="display: inline-block"></div>
            <div class="twothird column">
              <img
                style="border: none; width: 100%; height: auto; box-shadow: none;"
                src="@/assets/collectdata.png"
              />
            </div>
            <div class="onethird column guide">
              <p>Find a good spot for your UV sensor - somewhere without shadows or changing light.</p>
              <p>Bring all your materials with you, but don't cover the sensor with anything yet.</p>
              <p>Make sure the sun symbol on the sensor board is facing up!</p>
            </div>
          </div>
          <div class="right column">
            <button class="next buttonX" @click="Next()">Let's collect data!</button>
          </div>
        </div>
      </section>

      <section>
        <section>
          <div class="slideX">
            <div class="left column"></div>
            <div class="middle column">
              <div class="controlX">
                <label v-show="!solo" class="back buttonX" @click="Left()">&#10094; Back</label>
              </div>
              <div class="middle_row" style="height: 550px; vertical-align: top">
                <div class="half column">
                  <label class="titleX">Collect data</label>
                  <div class="column" style="width: 100%; height: 515px;">
                    <div
                      style="margin-top:50px; background-color: #DADDE2; width: 100%; height: 300px; "
                    >
                      <div
                        v-if="selectedSensor=='lux'"
                        v-bind:style="{backgroundColor: luxColor}"
                        style="transition: background-color 1s; display: inline-block; width: 45%; height: 90%; border-radius: 10px; margin: 15px;"
                      ></div>
                      <div
                        v-if="selectedSensor=='uv'"
                        style="display: inline-block; width: 45%; height: 90%; border-radius: 10px; margin: 15px; overflow: hidden; background-color: white; padding: 0 !important; text-align: center;"
                      >
                        <img
                          :style="arrowStyle"
                          style="transition: transform 2s, filter 1s; border: none; width: auto; height: 100%; box-shadow: none; margin: 0 !important;"
                          src="@/assets/uv_arrow.png"
                        />
                      </div>
                      <div class="column" style="display: inline-block; width: 45%; height: 90%;">
                        <select
                          v-model="selectedSensor"
                          style="width: 100%; height: 40px; margin: 0 !important; color: #185bbc; padding-left: 10px;"
                        >
                          <option value="uv">Show UV Index</option>
                          <option value="lux">Show Lux</option>
                        </select>
                        <div
                          class="lux preview"
                          style="background-color: white; height: 200px; width: 100%; border-radius: 10px; text-align: center; margin-left: 0 !important; box-shadow: none !important"
                        >
