                      ></div>
                      {{luxDataBaseline[3]}}
                    </td>
                  </tr>
                  <tr>
                    <td>{{formatText(checklist[0])}}</td>
                    <td>
                      <div
                        v-bind:style="{width: luxDataMaterial1[3]/100+'px'}"
                        style="height: 30px; background-color: blue; display: inline-block"
                      ></div>
                      {{luxDataMaterial1[3]}}
                    </td>
                  </tr>
                  <tr>
                    <td>{{formatText(checklist[1])}}</td>
                    <td>
                      <div
                        v-bind:style="{width: luxDataMaterial2[3]/100+'px'}"
